.whiteframe-z1,.v-expansion-panel{box-shadow:0 1px 3px 0 rgba(0,0,0,0.2),0 1px 1px 0 rgba(0,0,0,0.14),0 2px 1px -1px rgba(0,0,0,0.12)}.whiteframe-z2{box-shadow:0 1px 5px 0 rgba(0,0,0,0.2),0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.12)}.whiteframe-z3{box-shadow:0 1px 8px 0 rgba(0,0,0,0.2),0 3px 4px 0 rgba(0,0,0,0.14),0 3px 3px -2px rgba(0,0,0,0.12)}.whiteframe-z4{box-shadow:0 2px 4px -1px rgba(0,0,0,0.2),0 4px 5px 0 rgba(0,0,0,0.14),0 1px 10px 0 rgba(0,0,0,0.12)}.whiteframe-z5{box-shadow:0 3px 5px -1px rgba(0,0,0,0.2),0 5px 8px 0 rgba(0,0,0,0.14),0 1px 14px 0 rgba(0,0,0,0.12)}.v-expansion-panel{padding:0 24px;cursor:pointer;overflow:hidden;margin:0 0;background-color:#fff;border-bottom:1px solid rgba(0,0,0,0.12);width:100%;height:48px}.v-expansion-panel i{width:24px;position:absolute;right:0;top:-2px;-webkit-transition:-webkit-transform 0.3s cubic-bezier(0.55, 0, 0.1, 1);transition:-webkit-transform 0.3s cubic-bezier(0.55, 0, 0.1, 1);transition:transform 0.3s cubic-bezier(0.55, 0, 0.1, 1);transition:transform 0.3s cubic-bezier(0.55, 0, 0.1, 1), -webkit-transform 0.3s cubic-bezier(0.55, 0, 0.1, 1)}.v-expansion-panel .col-xs-3{margin-top:14px}.v-expansion-panel i.closed{-webkit-transform:rotate(0deg);transform:rotate(0deg)}.v-expansion-panel i.open{-webkit-transform:rotate(180deg);transform:rotate(180deg)}.v-expansion-title{color:rgba(0,0,0,0.87);font-size:15px}.v-expansion-secondary{color:rgba(0,0,0,0.54);font-size:15px;position:relative}



