.v-slider-container{width:100%;padding:8px 16px;position:relative}.v-slider-container *{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.v-slider{width:100%;background-color:rgba(0,0,0,0.26);height:2px;margin:16px 0;position:relative}.v-slider-bubble{border-radius:50%;width:32px;height:32px;position:absolute;color:#fff;padding:8px 0;text-align:center;left:0px;top:-26px;background-color:#3F51B5;z-index:5;cursor:pointer;-webkit-transition:all 0.25s cubic-bezier(0, 0, 0.2, 1);transition:all 0.25s cubic-bezier(0, 0, 0.2, 1);opacity:0}.v-slider-bubble.focus{-webkit-transition:opacity 0.25s cubic-bezier(0, 0, 0.2, 1);transition:opacity 0.25s cubic-bezier(0, 0, 0.2, 1);opacity:1}.v-slider-bubble::after{display:block;width:20px;height:20px;background-color:#3f51b5;content:" ";opacity:1;top:-12px;right:-6px;z-index:-1;position:relative;-webkit-transform:rotate(45deg);transform:rotate(45deg)}.v-slider-bubble.has-value{border-color:#3F51B5;background-color:#3F51B5}.v-slider-circle{border-radius:50%;width:16px;height:16px;position:absolute;border:2px solid rgba(0,0,0,0.26);left:8px;top:17px;background-color:#fff;z-index:5;cursor:pointer;-webkit-transition:all 0.25s cubic-bezier(0, 0, 0.2, 1);transition:all 0.25s cubic-bezier(0, 0, 0.2, 1)}.v-slider-circle.focus{width:24px;height:24px;left:4px;top:12px;-webkit-transition:none;transition:none}.v-slider-circle.has-value{border-color:#3F51B5;background-color:#3F51B5}.v-slider-indicator{position:absolute;width:calc(100% - 32px);background-color:#3F51B5;height:2px;margin:16px 0;top:8px;left:16px;-webkit-transform-origin:0 0;transform-origin:0 0;-webkit-transition:all 0.25s cubic-bezier(0, 0, 0.2, 1);transition:all 0.25s cubic-bezier(0, 0, 0.2, 1)}.v-slider-indicator.focus{-webkit-transition:none;transition:none}.slider-dot{border-radius:50%;width:6px;height:6px;background-color:rgba(0,0,0,0.54);display:inline-block;position:absolute}



